.Head {
    background-image: url(http://cdn.home-designing.com/wp-content/uploads/2015/05/white-interior.png);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    color: white;
    -webkit-box-shadow: inset 0 0 0 1000px rgba(0,0,0,.3);
            box-shadow: inset 0 0 0 1000px rgba(0,0,0,.3);
    font-size: .9rem;
}
.Head div:nth-child(2) {
    margin: 15vh 0;
    padding: 0 12px;
    text-align: center;
    border: 2px white solid;
}

.Head div:nth-child(2) p{
    letter-spacing: .4rem;
    margin-bottom: .2rem;
    font-size: .9rem;
}

.Head div:nth-child(2) h1{
    margin-top: .2rem;
}

.Head .btns {
    margin-top: 10px;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.Head .btns :nth-child(1){
    -ms-flex-positive: 10;
        flex-grow: 10;
}

.Head .btns :nth-child(2){
    -ms-flex-positive: 0;
        flex-grow: 0;
}

.Head .btns :nth-child(3){
    -ms-flex-positive: 10;
        flex-grow: 10;
}


